		this.updateNow();
		this.fnc_bell();
		setInterval(this.updateNow.bind(this) , 1000);
		setInterval(this.fnc_bell.bind(this) , 15000);

		axios.get('/back/SCR005001/Mob001List',{
				params: {
					search: this.user_id
				}
			})
		.then(response => {
			for(var x=0;x<response.data.length;x++){
				if(x==0){
					this.screen_name_001006=response.data[x].clm_screen_name;
					this.screen_authority_001006=response.data[x].screen_authority;
				}else if(x==1){
					this.screen_name_001008=response.data[x].clm_screen_name;
					this.screen_authority_001008=response.data[x].screen_authority;
				}else if(x==2){
					this.screen_name_001001=response.data[x].clm_screen_name;
					this.screen_authority_001001=response.data[x].screen_authority;
				}else if(x==3){
					this.screen_name_001002=response.data[x].clm_screen_name;
					this.screen_authority_001002=response.data[x].screen_authority;
				}else if(x==4){
					this.screen_name_001003=response.data[x].clm_screen_name;
					this.screen_authority_001003=response.data[x].screen_authority;
				}else if(x==5){
					this.screen_name_001004=response.data[x].clm_screen_name;
					this.screen_authority_001004=response.data[x].screen_authority;
				}else if(x==6){
					this.screen_name_001005=response.data[x].clm_screen_name;
					this.screen_authority_001005=response.data[x].screen_authority;
				}else if(x==7){
					this.screen_name_001007=response.data[x].clm_screen_name;
					this.screen_authority_001007=response.data[x].screen_authority;
				}
			}
		})
		.catch(error => {
			console.log(error)
		});
		axios.get('/back/SCR002002/RepositoryList')
		.then(response => {
			this.clm_repository_id_items = response.data;
		})
		.catch(error => {
			console.log(error)
		});

		var date = new Date();
		var year = date.getFullYear();
		var month = (date.getMonth() + 1);
		var day = date.getDate();

		var oneMonthAgo = new Date(date.setMonth(date.getMonth() - 1));
		var oneMonthyear = oneMonthAgo.getFullYear();
		var oneMonthmonth = (oneMonthAgo.getMonth() + 1);
		var oneMonthday = oneMonthAgo.getDate();

		oneMonthmonth = (oneMonthmonth < 10) ? "0" + String(oneMonthmonth) : oneMonthmonth;
		oneMonthday = (oneMonthday < 10) ? "0" + String(oneMonthday) : oneMonthday;

		month = (month < 10) ? "0" + String(month) : month;
		day = (day < 10) ? "0" + String(day) : day;

		axios.get('/back/SCR002002/RepositoryList')
		.then(response => {
			this.m_repository_id_items = response.data;
		})
		.catch(error => {
			console.log(error)
		});
		// if(Number(day) < 6){
		// 	this.start_date = oneMonthyear + '-' + oneMonthmonth  + '-15';
		// }else{
		// 	this.start_date = year + '-' + month  + '-01';
		// }
		// this.end_date = year + '-' + month  + '-' + day;
		this.getList();

		var link = this.$router.app.$route.query.from_location;
		var link_kor = '';
		if(link == 'mob001001'){
			link_kor = '생산계획'
		}else if(link == 'mob001002'){
			link_kor = '재단'
		}else if(link == 'mob001003'){
