			link_kor = '출하'
		}else if(link == 'mob001008'){
			link_kor = '자재출고'
		}

		if(String(this.user_agent).indexOf('samsung') > -1 || String(this.user_agent).indexOf('sm-p610') > -1 ){
			ReactNativeWebView.postMessage("MOB001006");
		}
	},
