		onClickClientOrderRow(event, data){
			var sum_count =0;
			for(var x=0; x<this.ReleaseCountItems.length;x++){
				if(data.item.clm_client_order_id==this.ReleaseCountItems[x].clm_stock_client_order_id && data.item.clm_seq==this.ReleaseCountItems[x].clm_stock_client_order_seq){
					sum_count =sum_count +Number(this.ReleaseCountItems[x].clm_release_count);
				}
			}
			var left_count =Number(data.item.clm_left_order_count)-sum_count;
			if(this.ReleaseCountItems[this.tmp_index].clm_stock_client_order_id == data.item.clm_client_order_id){
				//같은번호 눌렀을때
				this.clientOrderModal = false;
				return;
			}else if(left_count==0){
				this.ReleaseCountItems[this.tmp_index].clm_release_count=left_count;
				//가용 수량 없을때
				alert(data.item.clm_client_order_id+'의 주문 수량을 초과했습니다.');
				this.clientOrderModal = false;
				return;
			}else if(left_count<0){
				//가용 수량 없을때
				alert(data.item.clm_client_order_id+'의 주문 수량을 초과했습니다.');
				this.clientOrderModal = false;
				return;
			}else{
				this.ReleaseCountItems[this.tmp_index].clm_release_client_order_count = Number(left_count);
				// this.ReleaseCountItems[this.tmp_index].clm_before_release_count = Number(left_count);
				if(left_count >= Number(this.ReleaseCountItems[this.tmp_index].clm_release_count)){
					//주문수량 >= 출하 수량
				}else{
					//주문수량 < 출하 수량
					this.ReleaseCountItems.splice(this.tmp_index+1, 0,{
						clm_release_id:this.ReleaseCountItems[this.tmp_index].clm_release_id,
						clm_release_seq :this.ReleaseCountItems[this.tmp_index].clm_release_seq,
						lotno :this.ReleaseCountItems[this.tmp_index].lotno,
						clm_product_id : this.ReleaseCountItems[this.tmp_index].clm_product_id,
						clm_product_name :this.ReleaseCountItems[this.tmp_index].clm_product_name,
						clm_original_release_count:this.ReleaseCountItems[this.tmp_index].clm_original_release_count,
						clm_release_enable_count :this.ReleaseCountItems[this.tmp_index].clm_release_enable_count,
						clm_release_count :Number(this.ReleaseCountItems[this.tmp_index].clm_release_count)-Number(left_count),
						clm_before_release_count :Number(this.ReleaseCountItems[this.tmp_index].clm_release_count)-Number(left_count),
						clm_release_client_order_count :0,
						clm_release_client_id :'',
						clm_client_id:this.ReleaseCountItems[this.tmp_index].clm_client_id,
						clm_client_name :'',
						clm_joborder_id :this.ReleaseCountItems[this.tmp_index].clm_joborder_id,
						clm_client_order_id :this.ReleaseCountItems[this.tmp_index].clm_client_order_id,
						clm_stock_client_order_id :'',
						clm_stock_client_order_seq :'',
						clm_gubun : this.ReleaseCountItems[this.tmp_index].clm_gubun,
						clm_material_order_id:this.ReleaseCountItems[this.tmp_index].clm_material_order_id,
						clm_order_income_seq:this.ReleaseCountItems[this.tmp_index].clm_order_income_seq,
						clm_income_material_seq:this.ReleaseCountItems[this.tmp_index].clm_income_material_seq,
						clm_material_leftover_seq:this.ReleaseCountItems[this.tmp_index].clm_material_leftover_seq,
					});
					this.ReleaseCountItems[this.tmp_index].clm_release_count=left_count;
					this.ReleaseCountItems[this.tmp_index].clm_before_release_count=left_count;
				}
				this.ReleaseCountItems[this.tmp_index].clm_client_name = data.item.clm_client_name;
				this.ReleaseCountItems[this.tmp_index].clm_stock_client_order_id = data.item.clm_client_order_id;
				this.ReleaseCountItems[this.tmp_index].clm_stock_client_order_seq = data.item.clm_seq;
				this.ReleaseCountItems[this.tmp_index].clm_release_client_id = data.item.clm_client_id;
			}
			this.clientOrderModal = false;
		},
		changeReleasecount(index){
			//출하 수량 수정
			var error_1='N';
			var sum_count =0;
			var sum_stock_count =0;
			if(Number(this.ReleaseCountItems[index].clm_release_count)<=0){
				this.ReleaseCountItems[index].clm_release_count = Number(this.ReleaseCountItems[index].clm_before_release_count);
			}else if(Number(this.ReleaseCountItems[index].clm_release_count)% 1!= 0){
				this.ReleaseCountItems[index].clm_release_count = Number(this.ReleaseCountItems[index].clm_before_release_count);
			}
			for(var x=0; x<this.ReleaseCountItems.length;x++){
				if(this.ReleaseCountItems[index].clm_joborder_id==this.ReleaseCountItems[x].clm_joborder_id && index !=x){
					sum_count =sum_count +Number(this.ReleaseCountItems[x].clm_release_count);
				}
				if(this.ReleaseCountItems[index].clm_stock_client_order_id==this.ReleaseCountItems[x].clm_stock_client_order_id && this.ReleaseCountItems[index].clm_stock_client_order_seq==this.ReleaseCountItems[x].clm_stock_client_order_seq && index !=x){
					sum_stock_count =sum_stock_count +Number(this.ReleaseCountItems[x].clm_release_count);
				}
			}
			if(this.ReleaseCountItems[index].clm_gubun=='P' ){
				if( sum_stock_count>Number(this.ReleaseCountItems[index].clm_release_client_order_count) && this.ReleaseCountItems[index].clm_stock_client_order_id !=''){
					sum_stock_count = sum_stock_count- Number(this.ReleaseCountItems[index].clm_release_count);
					alert('고객제품주문 수량 '+this.ReleaseCountItems[index].clm_release_client_order_count+'을 초과하였습니다.');
					//this.ReleaseCountItems[index].clm_release_count=0;
					this.ReleaseCountItems[index].clm_release_count = this.ReleaseCountItems[index].clm_before_release_count;
					//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
					return;
				}
				if(sum_count+Number(this.ReleaseCountItems[index].clm_release_count)> Number(this.ReleaseCountItems[index].clm_release_enable_count)){
					//출하가능수량 초과
					if(this.ReleaseCountItems[index].clm_stock_client_order_id =='' || this.ReleaseCountItems[index].clm_gubun=='M'){
						alert('출하 가능 수량을 초과하였습니다.');
						this.ReleaseCountItems[index].clm_release_count = this.ReleaseCountItems[index].clm_before_release_count;
						//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
						//this.ReleaseCountItems[index].clm_release_count = Number(this.ReleaseCountItems[index].clm_release_enable_count)- sum_count;
						return;
					}else{
						var count=0;
						var count2=0;
						if(Number(this.ReleaseCountItems[index].clm_release_count)>Number(this.ReleaseCountItems[index].clm_release_client_order_count)){
							count =Number(this.ReleaseCountItems[index].clm_release_count)-Number(this.ReleaseCountItems[index].clm_release_client_order_count);
						}
						if(sum_stock_count+Number(this.ReleaseCountItems[index].clm_release_count)>Number(this.ReleaseCountItems[index].clm_release_enable_count)){
							count2 =sum_stock_count+Number(this.ReleaseCountItems[index].clm_release_count)-Number(this.ReleaseCountItems[index].clm_release_enable_count);
						}
						if(count==0 && count2!=0){
							alert('출하 가능 수량을 초과하였습니다.');
							this.ReleaseCountItems[index].clm_release_count = this.ReleaseCountItems[index].clm_before_release_count;
							//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
							//this.ReleaseCountItems[index].clm_release_count = Number(this.ReleaseCountItems[index].clm_release_enable_count)-(sum_stock_count-Number(this.ReleaseCountItems[index].clm_release_count));
							return;
						}else if(count!=0 && count2==0){
							console.log('2');
							alert(this.ReleaseCountItems[index].clm_stock_client_order_id+'의 주문 수량 '+Number(this.ReleaseCountItems[index].clm_release_client_order_count)+'을 초과하였습니다.');
							this.ReleaseCountItems[index].clm_release_count =Number(this.ReleaseCountItems[index].clm_release_client_order_count)-sum_stock_count;
							//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
							return;
						}else{
							if(count>=count2){
								console.log('3');
								//고객주문수량 > 출하가능
								alert('출하 가능 수량을 초과하였습니다.');
								this.ReleaseCountItems[index].clm_release_count = this.ReleaseCountItems[index].clm_before_release_count;
								//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
								//this.ReleaseCountItems[index].clm_release_count = Number(this.ReleaseCountItems[index].clm_release_enable_count)-sum_stock_count;
								return;
							}else{
								this.ReleaseCountItems[index].clm_release_count = this.ReleaseCountItems[index].clm_before_release_count;
								alert(this.ReleaseCountItems[index].clm_stock_client_order_id+'의 주문 수량 '+Number(this.ReleaseCountItems[index].clm_release_client_order_count)+'을 초과하였습니다.');
								//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
								//this.ReleaseCountItems[index].clm_release_count =Number(this.ReleaseCountItems[index].clm_release_client_order_count)-sum_stock_count;
							}
						}
					}
				}else if(this.ReleaseCountItems[index].clm_stock_client_order_id !=''){
					console.log('5');
					if(Number(this.ReleaseCountItems[index].clm_release_count)>Number(this.ReleaseCountItems[index].clm_release_client_order_count)){
						alert(this.ReleaseCountItems[index].clm_stock_client_order_id+'의 주문 수량 '+Number(this.ReleaseCountItems[index].clm_release_client_order_count)+'을 초과하였습니다.');
						//this.ReleaseCountItems[index].clm_release_count =Number(this.ReleaseCountItems[index].clm_release_client_order_count);
						this.ReleaseCountItems[index].clm_release_count = this.ReleaseCountItems[index].clm_before_release_count;
						//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
						return;
					}
				}
			}else{
				if(Number(this.ReleaseCountItems[index].clm_release_count)>Number(this.ReleaseCountItems[index].clm_release_enable_count)){
					alert('출하 가능 수량을 초과하였습니다.');
					this.ReleaseCountItems[index].clm_release_count = Number(this.ReleaseCountItems[index].clm_before_release_count);
					return;
				}
			}
			this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
