		getList() {
			this.selected_items=[];
			const vm = this
			this.loading = true	
			// this.save_yn='Y';		
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let items = axios.get('/back/MOB001006/List',{
						params: {
							keyword: this.keyword,
							start_date: this.start_date,
							end_date: this.end_date,
							page: page,
							mob:'mob001',
							PerPage: itemsPerPage,
							clm_user: this.$session.get('user_id')
						}
					}).then(
					response => {
						if (response.status == '200') {
							items = response.data
							setTimeout(() => {
								this.save_yn='N';
								this.loading = false
								this.document = items
							}, 500)
						}else{
							this.save_yn='N';
							this.loading = false
							Swal.fire({
								title:'서버가 불안정합니다.\n다시 조회해주세요.',
								icon: 'error'
							});
							this.document =[];
						}
					},
				)
			})
		},
