		material_typeChange(value){
			if(value=='M' && String(this.m_material_type)!='0006'&& String(this.m_material_type)!='0007'){
				//변경없음
				this.materialModal=true;
				this.materialHollModal=false;
			}else if(value=='M' && (String(this.m_material_type)=='0006' || String(this.m_material_type)=='0007')){
				// 합판 or MDF 로 변경
				this.m_h_s_material_thickness=this.m_s_material_thickness,
				this.m_h_s_material_width=this.m_s_material_width,
				this.m_h_s_material_height=this.m_s_material_height,
				this.m_h_s_material_holl_size=0,
				this.materialModal=false;
				this.materialHollModal=true;
			}else if(value=='H' && (String(this.m_material_type)=='0006' || String(this.m_material_type)=='0007')){
				//변경없음
				this.materialModal=false;
				this.materialHollModal=true;
			}else if(value=='H' && String(this.m_material_type)!='0006'&& String(this.m_material_type)!='0007'){
				// 합판 or MDF에서 변경
				this.m_s_material_thickness=this.m_h_s_material_thickness,
				this.m_s_material_width=this.m_h_s_material_width,
				this.m_s_material_height=this.m_h_s_material_height,
				this.m_h_s_material_holl_size=0,
				this.materialModal=true;
				this.materialHollModal=false;
			}
		},
		saveMaterialReg(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if(this.m_material_type == ''){
				alert('자재 유형이 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_s_material_width == ''){
				alert('자재 길이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_s_material_width)<1){
				alert('자재 길이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_s_material_height == ''){
				alert('자재 넓이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_s_material_height) < 1){
				alert('자재 넓이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_s_material_thickness == ''){
				alert('자재 높이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_s_material_thickness) < 1){
				alert('자재 높이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			axios.post('/back/SCR001004/SubMaterialReg', null, { params: {
				clm_m_material_type: this.m_material_type,
				clm_m_s_material_width: this.m_s_material_width,
				clm_m_s_material_height: this.m_s_material_height,
				clm_m_s_material_thickness: this.m_s_material_thickness
			}})
			.then(response => {
				console.log(response)
				if (response.status == '200') {
					this.loading_yn='N';
					if(this.v_m_material_order_id!=''){
						this.ViewItems[this.materialIndex].clm_material_id = response.data.id;
						this.ViewItems[this.materialIndex].clm_material_name =  response.data.clm_material_type + ' ' + response.data.name;
						this.ViewItems[this.materialIndex].clm_holl_size = 0;
						this.ViewItems[this.materialIndex].clm_material_type_name = response.data.clm_material_type,
						this.ViewItems[this.materialIndex].clm_order_count = '1';
						this.ViewItems[this.materialIndex].clm_order_bundle_count = '1';
						this.ViewItems[this.materialIndex].clm_cube_size = '1';
						this.ViewItems[this.materialIndex].clm_origin_cube_size = '1'; 
						
						this.ViewItems[this.materialIndex].clm_material_type = response.data.clm_material_type_id,
						this.ViewItems[this.materialIndex].clm_width = response.data.clm_width,
						this.ViewItems[this.materialIndex].clm_height = response.data.clm_height,
						this.ViewItems[this.materialIndex].clm_thickness = response.data.clm_thickness;
					}else{
						this.RegItems[this.materialIndex].clm_material_id = response.data.id;
						this.RegItems[this.materialIndex].clm_material_name =  response.data.clm_material_type + ' ' + response.data.name;
						this.RegItems[this.materialIndex].clm_holl_size = 0;
						this.RegItems[this.materialIndex].clm_material_type_name = response.data.clm_material_type;
						this.RegItems[this.materialIndex].clm_order_count = '1';
						this.RegItems[this.materialIndex].clm_order_bundle_count = '1';
						this.RegItems[this.materialIndex].clm_cube_size = '1';
						this.RegItems[this.materialIndex].clm_origin_cube_size = '1';
						this.RegItems[this.materialIndex].clm_material_type = this.m_material_type,
						this.RegItems[this.materialIndex].clm_width = Number( this.m_s_material_width),
						this.RegItems[this.materialIndex].clm_height = Number( this.m_s_material_height),
						this.RegItems[this.materialIndex].clm_thickness =  Number( this.m_s_material_thickness);
					}
					this.m_s_material_thickness = '',
					this.m_s_material_height = '',
					this.m_s_material_width = '',
					this.m_h_s_material_thickness = '',
					this.m_h_s_material_height = '',
					this.m_h_s_material_width = '',
					this.materialModal = false
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
		saveHMaterialReg(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if(this.m_material_type == ''){
				alert('자재 유형이 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_h_s_material_width == ''){
				alert('자재 길이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_h_s_material_width)<1){
				alert('자재 길이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_h_s_material_height == ''){
				alert('자재 넓이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_h_s_material_height) < 1){
				alert('자재 넓이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_h_s_material_thickness == ''){
				alert('자재 높이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_h_s_material_thickness) < 1){
				alert('자재 높이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_h_s_material_holl_size) < 0){
				alert('홀크기는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			axios.post('/back/SCR001004/SubMaterialReg', null, { params: {
				clm_m_material_type: this.m_material_type,
				clm_m_s_material_width: this.m_h_s_material_width,
				clm_m_s_material_height: this.m_h_s_material_height,
				clm_m_s_material_thickness: this.m_h_s_material_thickness
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					
					if(this.v_m_material_order_id!=''){
						if(Number(this.m_h_s_material_holl_size)>0){
							this.ViewItems[this.materialIndex].clm_material_name = response.data.clm_material_type + ' ' + response.data.name + '∅' + String(Number(this.m_h_s_material_holl_size)),
							this.ViewItems[this.materialIndex].clm_holl_size = String(Number(this.m_h_s_material_holl_size))
						}else{
							this.ViewItems[this.materialIndex].clm_material_name = response.data.clm_material_type + ' ' + response.data.name,
							this.ViewItems[this.materialIndex].clm_holl_size = '0'
						}
						this.ViewItems[this.materialIndex].clm_material_id = response.data.id;
						// this.ViewItems[this.materialIndex].clm_material_name =  response.data.clm_material_type + ' ' + response.data.name;
						this.ViewItems[this.materialIndex].clm_material_type_name = response.data.clm_material_type,
						this.ViewItems[this.materialIndex].clm_order_count = '1';
						this.ViewItems[this.materialIndex].clm_order_bundle_count = '1';
						this.ViewItems[this.materialIndex].clm_cube_size = '1';
						this.ViewItems[this.materialIndex].clm_origin_cube_size = '1'; 
						
						this.ViewItems[this.materialIndex].clm_material_type = this.m_material_type,
						this.ViewItems[this.materialIndex].clm_width = Number( this.m_h_s_material_width),
						this.ViewItems[this.materialIndex].clm_height = Number( this.m_h_s_material_height),
						this.ViewItems[this.materialIndex].clm_thickness =  Number( this.m_h_s_material_thickness);
					}else{
						if(Number(this.m_h_s_material_holl_size)>0){
							this.RegItems[this.materialIndex].clm_material_name = response.data.clm_material_type + ' ' + response.data.name + '∅' + String(Number(this.m_h_s_material_holl_size)),
							this.RegItems[this.materialIndex].clm_holl_size = String(Number(this.m_h_s_material_holl_size))
						}else{
							this.RegItems[this.materialIndex].clm_material_name = response.data.clm_material_type + ' ' + response.data.name,
							this.RegItems[this.materialIndex].clm_holl_size = '0'
						}
						this.RegItems[this.materialIndex].clm_material_id = response.data.id;
						// this.RegItems[this.materialIndex].clm_material_name =  response.data.clm_material_type + ' ' + response.data.name;
						this.RegItems[this.materialIndex].clm_material_type_name = response.data.clm_material_type;
						this.RegItems[this.materialIndex].clm_order_count = '1';
						this.RegItems[this.materialIndex].clm_order_bundle_count = '1';
						this.RegItems[this.materialIndex].clm_cube_size = '1';
						this.RegItems[this.materialIndex].clm_origin_cube_size = '1';
						this.RegItems[this.materialIndex].clm_material_type = response.data.clm_material_type_id,
						this.RegItems[this.materialIndex].clm_width = response.data.clm_width,
						this.RegItems[this.materialIndex].clm_height = response.data.clm_height,
						this.RegItems[this.materialIndex].clm_thickness = response.data.clm_thickness;
						this.RegItems[this.materialIndex].clm_material_type = this.m_material_type,
						this.RegItems[this.materialIndex].clm_width = Number( this.m_h_s_material_width),
						this.RegItems[this.materialIndex].clm_height = Number( this.m_h_s_material_height),
						this.RegItems[this.materialIndex].clm_thickness =  Number( this.m_h_s_material_thickness);
					}
					this.m_s_material_thickness = '',
					this.m_s_material_height = '',
					this.m_s_material_width = '',
					this.m_h_s_material_thickness = '',
					this.m_h_s_material_height = '',
					this.m_h_s_material_width = '',
					this.materialHollModal = false
				}
			})
			.catch(error => {
				console.log(error)
			})
		}
	}
}
</script>